@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
h1{font-weight:700}p,a{font-family:"Montserrat", sans-serif;font-size:1.6rem}a,a:link,a:hover,a:visited,a:active{text-decoration:none}a:hover{transition:all 0.3s ease-in-out}.section-title{margin:0px;margin-bottom:4.5rem;font-size:4rem;font-weight:bold;text-transform:uppercase}@media (max-width: 37.5em){.section-title{font-size:2.8rem}}.dark-blue-text{color:#272341 !important}.text-color-main{background-image:linear-gradient(135deg, #02aab0 0%, #00cdac 100%);-webkit-background-clip:text;-webkit-text-fill-color:transparent}@media (max-width: 37.5em){.text-color-main{background-image:none;-webkit-text-fill-color:#00cdac}}.text-color-main:hover{transform:translateX(2px)}.cta-btn{display:inline-block;position:relative;padding:0.8rem 1.6rem;font-weight:bold;line-height:1;z-index:1;transition:all cubic-bezier(0.19, 1, 0.22, 1) 0.6s}.cta-btn::after{content:"";display:block;position:absolute;width:0px;height:100%;left:0;bottom:0;z-index:-1;transition:all cubic-bezier(0.19, 1, 0.22, 1) 0.3s}.cta-btn--hero{background-image:linear-gradient(135deg, #02aab0 0%, #00cdac 100%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;border-width:2px;border-style:solid;border-image:linear-gradient(135deg, #02aab0 0%, #00cdac 100%);border-image-slice:1;cursor:pointer}@media (max-width: 37.5em){.cta-btn--hero{background-image:none;border:2px solid #00cdac;-webkit-text-fill-color:#00cdac}}.cta-btn--hero::after{background-image:linear-gradient(135deg, #02aab0 0%, #00cdac 100%)}@media (max-width: 37.5em){.cta-btn--hero::after{background-image:none}}.cta-btn--hero:hover{-webkit-text-fill-color:#fff;text-decoration:none}@media (max-width: 37.5em){.cta-btn--hero:hover{-webkit-text-fill-color:#00cdac}}.cta-btn--hero:hover::after{width:100%}.cta-btn--hero:hover a{-webkit-text-fill-color:#fff}.cta-btn--hero a{text-decoration:none}.cta-btn--hero a:hover{-webkit-text-fill-color:#fff}.cta-btn--resume{color:#fff;border:2px solid #fff}.cta-btn--resume::after{background:#fff}.cta-btn--resume:hover{color:#00cdac;text-decoration:none}.cta-btn--resume:hover::after{width:100%}.up i{color:#272727}#hero{height:90vh;display:flex;padding-left:0 !important;align-items:center;border-bottom:0px;background:#fff;font-weight:400;color:#272341;padding:0rem 5.6rem;margin-bottom:0;top:0;left:0;bottom:0;right:0;z-index:-1}@media (max-width: 37.5em){#hero{padding:0rem 1.6rem}}#hero .hero-title{font-size:3.2rem;font-weight:700;margin-bottom:3.2rem;text-align:left}@media (max-width: 75em){#hero .hero-title{font-size:2.5rem}}@media (max-width: 56.25em){#hero .hero-title{font-size:2.5rem;text-align:center}}@media (max-width: 37.5em){#hero .hero-title{font-size:2rem;line-height:1.5}}@media (max-width: 20em){#hero .hero-title{font-size:1rem}}#hero .hero-cta{display:flex;margin-top:4rem}@media (max-width: 56.25em){#hero .hero-cta{justify-content:center}}#hero .hero-cta a{font-size:1.4rem}@media (max-width: 37.5em){#hero .hero-cta a{font-size:1rem}}#hero .container{margin-top:8rem}body{margin:0;font-family:"Montserrat", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body,#root{height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

